@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  font-family: 'Roboto', sans-serif;
}

button {
  background-color: #3575d4; /* Green */
  border: none;
  color: white;
  padding: 10px 25px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  border-radius: 2px;
}
.form-field {
    display: flex;
    background-color: #e7e7e7;
    border-radius: 2px;
    box-shadow: 2px 2px 5px black;
    padding-left: 10px;
    width: 500px;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
}

.difficulty {
    list-style-type: none;
    padding: 0;
}

label {
    margin-left: 10px;
    margin-right: 10px;
}

.texts {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.text-label {
    display: block;
    width: 50px;
}

.remove-button {
    margin-left: auto;
}

.submit-buttons button {
    width : 200px;
    margin: 10px;

}

.submit-buttons {
    text-align: center;
}
#schedule {
    margin-top: 50px;
    margin-bottom: 100px;
}

#range {
    text-align: center;
}

.event {
    width: auto;
}

.event-list {
    display: flex;
    list-style-type: none;
    padding: 0;
    margin-left: auto;
    justify-content: center;
}

.name {
    flex: 0 0 15em;
}

.start {
    flex: 0 0 8em;
}

.dash {
    flex: 0 0 3em;
}

.end {
    flex: 0 0 8em;
}

